.card-plot {
    display: flex;
    /* justify-content: space-around; */
    column-gap: 33px;
    row-gap: 20px;
    flex-wrap: wrap;
    padding: 20px;
}
.to-remove-from-dashboard{
  opacity:0.3;
  
}
  
.card {
  flex: 0 1 25%;
  margin-bottom: 6px;
  width: calc(31% - 10px);
  /* font-size: 17px; */
  border: unset;
  /* margin-right: auto; */
}

.disable-card{
  pointer-events: none;
}

.toStore{
  box-shadow: 0px 0px 5px 2px black;
}

.hero-add-card-close-btn-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.delete-card-btns-set-container{
  display: flex;
  flex-direction: row-reverse;
}

.hero-component-container{
  display:flex;
}

.hero-options-container{
  display:flex; 
  width:100%;
}

.hero-component-choose-hero-for-equipment{
  display: flex; 
  list-style-type: none;
  padding-left: 0px; 
  width: calc(70% - 10px);
  overflow: auto; 
  align-items: center;
}

.title-num-heroes-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 45px;
}

.edit-card-name{
  pointer-events:none;
}

.title-of-equipment-set-container {
  width: 100%;
}

.num-heroes-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  max-width: calc(55% - 10px);
}

.hero-pile-active {
  width: fit-content;
  /* height: 68px; */
}

.three-heroes-show-img{
  width: 30px;
}

.img-hero{
  width: 30px;
}

.three-heroes-equipment-set-shown {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  margin-top: 5px;
  margin-bottom: 0px;
  padding-left: 5px;
  cursor: pointer;
}

.all-heroes-equipment-set-shown {
  display: flex;
  overflow: auto;
  list-style-type: none;
  margin-top: 5px;
  margin-bottom: 0px;
  padding-left: 5px;
}

.heros-pile-container {
  /* height: 100%; */
  display: flex;
  align-items: center;
}

.number-of-heroes-pile {
  cursor: pointer;
}

.number-of-heroes-pile h4 {
  margin-top: 0px;
  margin-bottom: 0px;
}

.heroes-attached-expand {
  display: flex;
  align-items: center;
  width: 100%;
  /* height: 68px; */
}

.close-btn-heroes-attached {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  /* border: solid; */
  width: 20px;
}

.equipment-list-category-list-stats-container {
  display: flex;
  justify-content: space-between;
  width: calc(100% - 6px);
  cursor: pointer;
}

.equipment-list-category-list-container {
  /* width: calc(65% - 10px); */
  width: 100%;
}

.summary-equipment-stats-container {
  width: calc(35% - 10px);
}

.equipment-list {
  display: flex;
  list-style-type: none;
  padding-left: 5px;
  flex-wrap: wrap;
}

.equipment-ind {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 30%;
  margin-bottom: 8px;
}

.skill-imgs{
  width:20px;
  height:20px;
}

.skill-imgs-name{
  text-align: center;
}

.rating-equipment-category-container{
  
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  padding-left: 5px;
}

.equipment-cateory-list {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  padding-left: 5px;
  margin-right: 8px;
  cursor: pointer;
  /* flex-wrap: wrap; */
}

.equipment-category-ind {
  /* flex: 30%; */
  margin-left: 9px;
  margin-bottom: 8px;
  border: solid 2px;
  padding: 5px;
  border-radius: 999px;
}

.equipment-category-ind-name {
  margin-bottom: 0px;
  margin-top: 0px;
}

.react-star-rate{
  font-size:28px !important;
}

.react-star-rate .react-star-rate__star--full{
  color:black !important;
}

.react-star-rate .react-star-rate__star__first{
  color:black !important;
}

.react-star-rate .show-totals__star--full{
  color:black !important;
}

.react-star-rate .show-totals__star__first{
  color:black !important;
}

.react-star-rate.show-totals{
  pointer-events: none;
}

.star-rating-icon-num-of-users-rate{
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
}

.number-of-users-rated{
  text-decoration:underline;
  font-style:italic;
  font-size:14px;
}

.summary-equipment-stats-pos {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-left: 5px;
  width: 100%;
}

.top-3-items-container {
  /* margin-top: 5px; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.top-3-items-icon-num-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.top-3-items-header {
  margin-top: 5px;
  margin-bottom: 5px;
}

.top-3-items-icon-num {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  /* width: 100%; */
}

.equipment-stat-pos-ind {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 12px;
}

.summary-equipment-stats-neg {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-left: 5px;
  width: 100%;
}

.bottom-3-items-container {
  /* margin-top: 5px; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.bottom-3-items-icon-num-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.bottom-3-items-icon-num {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bottom-3-items-header {
  margin-top: 5px;
  margin-bottom: 5px;
}

.equipment-stat-neg-ind {
  display: flex;
  /* flex-direction: row-reverse; */
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 12px;
}

.bottom-card-container{
  display:flex; 
  flex-direction:row; 
  width:100%;
}

.more-information-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  width: calc(100% - 35px);
}

.no-display{
  visibility:hidden;
}

.expanded-card-container{
  margin-top: 10px; 
  height: 300px;
}

.switch-show-chart{
  margin-left: unset !important;
}

.grid-plot-list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-left: 9px;
  width: calc(100% - 5px);
  /* height: 100%; */
  height: 258px;
  overflow: auto;
}

.grid-plot-header {
  display: flex;
  justify-content: flex-end;
  margin-right: 3px;
}

.grid-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 9px;
  margin-right: 7px;
  cursor: pointer;
}

.toggle-visual-equipment-category {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 10px;
}

.bar-chart-container{
  height: 250px; 
  width: 400px;
}

/* .select-card-for-dashboard{

} */

@media (hover: hover){
  .select-card-for-dashboard{
    display: block;
  }
  .select-card-dashbaord-no-touch{
    display: none;
  }
}


@media (hover: none){
  .select-card-for-dashboard{
    display: none;
  }
  .select-card-dashbaord-no-touch{
    display: block;
    margin: 9px;
  }
}



::-webkit-scrollbar {
  width: 0.7em;
  height: 0.7em;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: hsl(359 100% 0% /1);
  border-radius: 100vw;
}

::-webkit-scrollbar-thumb:hover {
  background: hsl(359 100% 0% /4);
}

