.component-parent-container{
    height:700px;
}

.filter-component{
    height:calc(100% - 30px);
}

.save-choices-btn{
    display:flex;
    justify-content: flex-end;
}

.filter-form-contents{
    display: flex;
    align-items: flex-end;
}

.filter-form-select-box{
    display: flex;
    flex-direction: column;
    width: calc(75% - 10px);
}

.filter-hierachy{
    display: flex;
    margin-left:10px;
}

.navigation-dashboard-options{
    display:flex;
    justify-content: flex-end;
}

.save-changes-btn{
    
    /* margin-left: auto; */
    display: flex;
    justify-content: space-between;
    width: 100%; 
    white-space: nowrap;
}

.search-dashboard-canvas{
    display:flex;
    justify-content: center;
    align-items: flex-end;
    width: calc(100% - 10px);
    margin-bottom: 20px;
}

.search-dashboard{
    width: calc(40% - 10px);
    
}

.filter-accordion{
    margin-top: 10px;
    width: calc(60% - 10px);
}

@media (max-width: 430px){
    .MuiButton-text{
        font-size: 12px !important;
    }
    
}

@media (max-width: 380px){
    .MuiButton-text{
        font-size: 11px !important;
    }
    
}

@media (max-width: 325px){
    .MuiButton-text{
        font-size: 11px !important;
    }
    
}